body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif; 
  /* -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; */
}

code {
  /* font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace; */
}

* {
  --primary-color: #f8f8f3; 
  --secondary-color: plum;
  --primary-font: 'Raleway';
}

body{
  object-fit: cover;
  height: 100vh;
  background-color: var(--secondary-color);
}

#root {
  height: -webkit-min-content;
  height: min-content;
  height: 100vh;
}

.App {
  text-align: center;
  height: 100vh;
}

.loader-wrapper {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: var(--secondary-color);
  display: flex;
  align-items: center;
}

.loader-box{
  margin: auto;
  text-align: center;
}

.loaded {
  opacity: 1;
}

/* Home */

.home-div{
  position: relative;
  background-color: var(--primary-color);
 

}

.home-bcgs-color {
  width: 100%;
  height: 100%;
  display: flex;
  object-fit: cover;
}

.home-bcgs-color > * {
  color: var(--primary-color)
}


.home-bcgs-bg{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-wrap: nowrap;
  overflow: hidden;
  object-fit: cover;
  z-index: 1;
}

.pix-1000{
  width: 100%;
  height: 0px;
  overflow: hidden;
}

.bcg-1-bridge{
  position: absolute;
  top: 0;
  left: 0px;
  display: flex;
  flex-wrap: nowrap;
  overflow: hidden;
  width: 100%;
  height: 100%;
  object-fit: contain;
  opacity: 0
}

.bcg-1-bridge:hover{
  transition: all 250ms ease-in-out;
  opacity: 1;
}

.bcg-1{
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  background-size: cover;
  height: 100%;
}

.bcg-2 {
  position: relative;
  width: 100%;
  z-index: 1;
  object-fit: contain;
  object-position: left;
}


/* Header & Nav bar ---------- */

.hidden-intro {
  position: absolute;
  top: 0;
  right: 0;
  width: 50%;
  margin: auto;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
  z-index:1 

}

.hidden-blurb {
  width: 80%;
  margin: 0 auto;
}


.mouse {
  position: absolute;

  bottom: 0;
  font-style: italic;
}

.App-header{
  top: 0;
  z-index: 99999;
}

.Header-contents{
  display: flex;
  justify-content: space-between ;
  margin: auto 10px;
}

.Navigator{
  display: flex;
  width: 50%;
  margin: auto 10px;
}

.nav-links{
  display: flex;
  justify-content: space-between;
  width: 100%;
  color: orange
}

.mobile-toggle {
  width: 1%
}

/* body and body elements */

.App-body {
  background-color: var(--primary-color);
  display: flex;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
  /* height: 100%; */
  
}

.body-divs{
  margin: 30px 30px;
}

.body-texts{
  font-size: 18px;
  line-height: 1.5;
  font-family: var(--primary-font)
}

.bold-and-left{
  font-weight: bold;
}

/* Resume Page -------------------- */

.skills {
    display: flex;
}

.skills-blocks{
  width: 50%;
}

.skills--lists > div{
  padding: 20px 0 0 0;
  border-bottom: 3px solid orange;
}

.skills--lists > div:last-child{
  border-bottom: 0px;
}

.technologies{
  justify-content: space-around;
  margin: 50px auto;
  

}


/* insert border radius or any height width px before width or pictures will spill out the window*/
.technologies-logos{
  height: 100px;
  width: 100px;
  border-radius: 50px;
  margin: 8px 8px;
}

.resume-style {
  -webkit-filter: blur(5px);
          filter: blur(5px)
}

/* Portfolio & apps ---------- */
.container--description{
  margin-left: 20%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: left;
}

.developer-github {
  width: 100%;
  margin: 0px auto;
}

.app-box{
  display: flex;
  margin: 0 auto;
  margin-top: 5px;
  padding: 15px 0 15px 0;
  border-bottom: 3px solid orange;
  width: 80%;
}

.app-box:last-child{
  border-bottom: 0px

}

.container_wrapper{
  margin: auto auto;
  width: 40%;
  border-radius: 5px;
  position: relative;
}

.container--picture{
  width: 100%;
  -webkit-filter: blur(5px);
          filter: blur(5px)
}

.container--overlay{
  position: absolute;
  top: 0 ;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  opacity: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.container--overlay:hover{
  opacity: 1;
  transition: all 250ms ease-in-out;
}

.container---blurb{
  color: white;
  margin: 0px auto;
  width: 80%;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

.project-title {
  display: flex;
}

.project-title-frame{
  width: 100%;
}

/* About me */
.aboutme{
  display: flex;
  margin: 20px 20px;
  justify-content: center;
}

.aboutme-blocks{
  width: 50%;
  margin: auto 15px;
  justify-content: center;
}

.aboutme--titles{
  margin: auto 10px;
}

.aboutme--quotes{
  font-style: italic;
  font-size: 22px;
}

.aboutme--facts{
  font-size: 22px;
}

.aboutme--gifs{
  display: flex;
  justify-content: space-around;
  margin: 50px auto
}

.gifs{
  border-radius: 200px;
  width: 15%
}

.profile {
  border-radius: 20px;
  border: orange 5px solid;
}

/* About Me - Carousels CSS ------------ */

.aboutme--carousel{
  border: orange 3px solid;
  border-radius: 25px;
}

.carousel-container {
  height: 200px;
  display: flex;
  justify-content: center;
}

.carousel--div{
  width: 80%;
  margin: auto auto;
  display: flex;
  flex-wrap: nowrap;
  overflow: hidden;
  
}

.carousel---block{
  min-width: 100%;
  width: 100%;
  transition: 1s cubic-bezier(0.1, 0.1, 0.1, 1.5);
  margin: auto;
}

.carousel-item {
  width: 100%;
  margin: auto auto;
  transition: 1s cubic-bezier(0.1, 0.1, 0.1, 1.5);
}

/* Footer ----------------------- */

.App-footer {
  padding: 15px 15px;
  display: flex;
  justify-content: space-around;
}

/* Pictures and logos ---------------- */
.logos{
  display: flex;
  justify-content: space-around;
  width: 25%
}

.logos-contactpg{
  width: 100%;
  padding: 50px auto;
  display: flex;
  justify-content: space-between;
  background-color: orange;
  opacity: 0.5;
  border-radius: 10px;
}

.logos-contactpg:hover{
  transition: all 250ms ease-in-out;
  opacity: 1;

}

.logos-icons{
  margin: auto auto;
}

/* Links */
.App-link {
  color: #61dafb;
}

.links{
  transition: ease-in-out 0.2s;
  font-size: 25px;
  font-family: var(--primary-font);
}

.links:hover{
  font-size: 29px;
  text-decoration: underline;
  text-underline-offset: 8px;
}

.headings:hover{
  text-decoration: underline;
  text-underline-offset: 8px;
}


.links-selected{
  color:var(--primary-color);
}

a {
  text-decoration: none;
  width: 100%;
  color: black
}

a:link{
  text-decoration: none;
}

a:visited{
  text-decoration: none;
  color: black
}

a:active{
  color: black
}

a:hover{
  transition: all 250ms ease-in-out;
}

.github-links {
  border-radius: 50px;
  margin: 20px auto;
  width: 20%;
}


/* Fonts */
.h1 {
  font-family: var(--primary-font);
  font-size: 50px;
  font-weight: 600;

}

.h2{
  color: black;
  font-family: var(--primary-font);
  font-weight: 600;
}

h3{
  font-family: 'Rock Salt', cursive;
}

h4, h5{
  margin: 10px 0;
  font-family: copperplate;
}

.home-pg-font {
  font-size: 20px;
  color: var(--primary-color)
}

.home-transitions{
  transition: ease-in-out 1.5s
}


@media screen and (max-width: 1500px) {
  .container---blurb{
    font-size: 25px
  }
}

@media screen and (max-width: 1200px) {
  .Navigator{
    width: 75%
  }

  .container---blurb{
    font-size: 20px
  }

  .aboutme {
    display: block
  }

  .aboutme-blocks{
    width: 100%;
    margin: auto auto;
    margin-bottom: 50px
  }

  .profile{
    width: 90%
  }

  .skills{
    display: block;
  }

  .skills-blocks{
    width: 100%;
    margin: auto auto;
  }
}

@media screen and (max-width: 1000px) {
  .container---blurb{
    font-size: 17px
  }

  .project-title{
    margin-bottom: 0px;
    display: block
  }

  .project-title-frame{
    width: 100%;
  }

  .Header-contents{
    display: block;
    /* padding: 25px; */
  }
  
  .Navigator{
    display: flex;
    width: 100%;
    margin: 0 auto;
  }
  
  .h2{
    margin: auto 0px;
  }

  .headings{
    margin: 25px auto;
  }

  .home-bcgs-bg {
    height: 0px;
    position: relative;
  }

  .home-bcgs {
    display: block;
  }

  .hidden-intro{
    height: 0px;
    width: 0px;
    overflow: hidden;
  } 
  
  .pix-1000{
    width: 80%;
    height: 100%;
    margin: 0 auto;
  }

  .home-bcgs-color{
    transition: ease-in-out 1s;
  }
}
  

@media screen and (max-width: 850px){
  .container---blurb{
    font-size: 14px
  }

  /* USE BLOCK INSTEAD OF INLINE FOR FLEX FOR BORDER TO SHOW */
  .app-box {
    display: block;
    width: 100%; 
    border-bottom: 3px solid orange;

  }

  .container_wrapper{
    margin: 0px auto;
    width: 75%;
  }

  .container---blurb{
    font-size: 25px
  }

  .container--description{
    margin: 0 auto;
    text-align: center;
  }

  .container--picture{
    width: 100%;
    
  }

  .github-links {
    width: 10%;
  }

}

@media screen and (max-width: 800px) {

  .footer-font {
    width: 20%
  }

  .icon-align {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .technologies-logos{
    height: 50px;
    width: 50px
  }
}

@media screen and (max-height: 800px){
  .bcg1{
    position: relative;
    width: 100%

  }
}

@media screen and (max-width: 700px){
  .footer-font{
    margin: auto 0px;
    width: 30%
  }

  .links{
    margin-bottom: 25px;
  }

  .container-description{
    text-align: center;
    margin: 0 auto;
    display: block;
  }

  .logos {
    justify-content: space-around;
    margin: 0px auto
  }

  .Navigator{
    display: block;
  }

  .Header-contents{
    padding: 2px;
    margin: 0 auto;
  }

  .mobile-toggle {
    width: 100%;
    height: 35px;
  }

  .mobile-options {
    background-color: black;
    width: 35px;
    height: 5px;
    margin: 5px auto;
  }

  .nav-links{
    opacity: 0;
    height: 1px;
  }

  .mobile-effect{
    transition: all 0.75s ease-in-out;
    opacity: 1;
    text-align: center;
    line-height: 60px;
    width: 100%;
    display: table;
    margin: 10px auto;
  }

  #mobile-menu.active .mobile-options:nth-child(2){
    transition: all 0.5s ease-in-out;
    opacity: 0;
  }

  #mobile-menu.active .mobile-options:nth-child(1){
    transition: all 0.5s ease-in-out;
    -webkit-transform: translateY(10px) rotate(45deg);
            transform: translateY(10px) rotate(45deg);
  }

  #mobile-menu.active .mobile-options:nth-child(3){
    transition: all 0.5s ease-in-out;
    -webkit-transform: translateY(-10px) rotate(-45deg);
            transform: translateY(-10px) rotate(-45deg);
  }  


  .bcg-2{
    object-fit: cover;
    object-position: 18%;
    height: 50vh;
  }
}

@media screen and (max-width: 600px) {
  .container---blurb{
    font-size: 20px
  }

  .profile{
    width: 95%
  }

  .gifs {
    width: 25%
  }

  .bcg-2{
    object-fit: cover;
    object-position: 15%;
    height: 40vh;
  }
}

@media screen and (max-width: 500px){
 
  .links{
    margin-bottom: 25px;
  }

  .container---blurb{
    font-size: 20px
  }

  .body-divs{
   margin: 10px 10px;
  }

  .body-texts{
    width: 95%;
    margin: 10px auto
  }

  .logos {
    width: 60%;
  }

  .logos-icons{
    width: 75%
  }

  .footer-font {
    width: 40%
  }

}

@media screen and (max-width: 400px){
  .container---blurb{
    font-size: 15px
  }

  .logos {
    display: inline;
  }

  .logos-icons {
    width: 25%
  }

  .icon-align {
    margin: 0 auto;
  }

}
.loader{
    
    --color1: black;
    
    /* -color2:#FAFAF9;; */
    /* --color2: #8F6513;
    --color3: #650B8F;
    --color4: #0B408F;
    --color5: #718F13; */
    --_mask: no-repeat radial-gradient(#000 68%,#0000 71%);
    width: 200px;
    margin: auto;
    aspect-ratio: 1;
    background: conic-gradient(from 36deg,
      var(--color1)0%,var(--color1)20%,
      var(--color1)20%,var(--color1)40%,
      var(--color1)40%,var(--color1)60%,
      var(--color1)60%,var(--color1)80%,
      var(--color1)80%)
      no-repeat;
    /*background-size: 200% 200%;*/
    -webkit-mask: var(--_mask),var(--_mask),var(--_mask),var(--_mask),var(--_mask);
    -webkit-mask-size: 25% 25%;
    -webkit-animation:
      
      load 3s infinite ease-in-out;
            animation:
      
      load 3s infinite ease-in-out;
  }
  
  @-webkit-keyframes load{
    0%, 100%{-webkit-mask-position:50% 0%, 100% 38%, 82% 100%, 18% 100%, 0% 38%;}
    20%{-webkit-mask-position:100% 38%, 82% 100%, 18% 100%, 0% 38%, 50% 0%;}
    40%{-webkit-mask-position:82% 100%, 18% 100%, 0% 38%, 50% 0%, 100% 38%;}
    60%{-webkit-mask-position:18% 100%, 0% 38%, 50% 0%, 100% 38%, 82% 100%;}
    80%{-webkit-mask-position:0% 38%, 50% 0%, 100% 38%, 82% 100%, 18% 100%;}
  }
  
  @keyframes load{
    0%, 100%{-webkit-mask-position:50% 0%, 100% 38%, 82% 100%, 18% 100%, 0% 38%;}
    20%{-webkit-mask-position:100% 38%, 82% 100%, 18% 100%, 0% 38%, 50% 0%;}
    40%{-webkit-mask-position:82% 100%, 18% 100%, 0% 38%, 50% 0%, 100% 38%;}
    60%{-webkit-mask-position:18% 100%, 0% 38%, 50% 0%, 100% 38%, 82% 100%;}
    80%{-webkit-mask-position:0% 38%, 50% 0%, 100% 38%, 82% 100%, 18% 100%;}
  }
  @-webkit-keyframes back{
    0%, 100%{background-position: 0 0;}
    25%{background-position: 0 100%;}
    50%{background-position: 100% 100%;}
    75%{background-position: 100% 0;}
  }
  @keyframes back{
    0%, 100%{background-position: 0 0;}
    25%{background-position: 0 100%;}
    50%{background-position: 100% 100%;}
    75%{background-position: 100% 0;}
  }
  
